      <span><img src="../../../assets/main_images/tx.png" alt=""><strong>系统管理员</strong></span>
      <span class="logout">
        <router-link to="/login" @click.native="delLogin">
          退出系统
        </router-link>
      </span>
    </div>
    <!--右下角导航-->
    <ul class="nav">
      <li v-for="(item,index) in data" :key="index" style="position: relative" @click="littleNavState=item.meta.id;theIndex = index" ref="nav">
        <router-link :to="item.url">
          <p class="navP" :class="{navActive :navActiveState === item.meta.id || littleNavState === item.meta.id}">
            <!--            <strong :style="{backgroundPosition:item.backPosition }"></strong>-->
            <span>{{item.name}}</span>
          </p>
        </router-link>
        <div class="littleNav" ref="littleNav" v-show="littleNavState === item.meta.id && item.children.length !== 0">
          <div v-for="(item2,index) in item.children" :key="index" @click.stop="littleNavState = -1;theIndex = -1">
            <router-link :to="item2.url">
              <p class="littleNavP">
                <strong :class="item2.backPosition" class="iconfont"></strong>
                <span>{{item2.name}}</span>
              </p>
            </router-link>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

