          url: '/main/projectApproval/proApp',
          backPosition: '0 120px',
          children: [],
          meta: {
            id: 5
          }
        },
        {
          name: '人类活动遥感监测',
          url: '#',
          backPosition: '0 180px',
          meta: {
            id: 6
          },
          children: [
            {
              name: '人类活动信息提取',
              url: '/main/monitor/extracTable',
              backPosition: 'icon-xinxitiqu',
              meta: {
                id: 7
              }
            },
            {
              name: '人类活动核查',
              url: '/main/monitor/activitiesInspectTable',
              backPosition: 'icon-jishijihechaxun',
              meta: {
                id: 8
              }
