              }
            },
            {
              name: '人类活动专题报告分析',
              url: '/main/monitor/reportAnalysisTable',
              backPosition: 'icon-fenxipingbi',
              meta: {
                id: 11
              }
            },
            {
              name: '人类活动干扰评价',
              url: '/main/monitor/evaluationList',
              backPosition: 'icon-pingjia',
              meta: {
                id: 12
              }
            }
          ]
        },
        {
          name: '核查任务管理',
          url: '#',
          backPosition: '0 90px',
          meta: {
            id: 13
          },
          children: [
            {
              name: '核查影像管理',
              url: '/main/dynamicManage/imageManagement',
              backPosition: 'icon-gongchengyingxiangguanli',
              meta: {
                id: 14
              }
            },
            {
              name: '核查区域管理',
              url: '/main/dynamicManage/areaManagement',
              backPosition: 'icon-quyu',
              meta: {
                id: 15
              }
            },
            {
              name: '核查任务管理',
              url: '/main/dynamicManage/taskManagementTable',
              backPosition: 'icon-renwuguanli',
              meta: {
                id: 16
              }
            },
            {
              name: '核查任务调度',
              url: '/main/dynamicManage/taskScheduler',
              backPosition: 'icon-renwutiaodu',
              meta: {
                id: 17
              }
            },
            {
              name: '野外工作监管',
              url: '/main/dynamicManage/workRegulation',
              backPosition: 'icon-jianguan f14',
              meta: {
                id: 18
              }
            },
            {
              name: '核查成果审核',
              url: '/main/dynamicManage/achievementTable',
              backPosition: 'icon-shenhe1',
              meta: {
                id: 19
              }
            },
            {
              name: '核查数据同步与统计分析',
              url: '/main/dynamicManage/dataAnalysis',
              backPosition: 'icon-tongjifenxi',
              meta: {
                id: 20
              }
            }
          ]
        },
        {
          name: '系统管理',
          url: '/main/systemManage/userManage',
          backPosition: '0 30px',
          meta: {
            id: 21
          },
          children: [
            // {
            //   name: '用户管理',
            //   url: '',
            //   backPosition: '0 -160px',
            //   meta: {
            //     id: ''
            //   }
            // },
            // {
            //   name: '角色管理',
            //   url: '',
            //   backPosition: '0 -160px',
            //   meta: {
            //     id: ''
            //   }
            // },
            // {
            //   name: '权限管理',
            //   url: '',
            //   backPosition: '0 -160px',
            //   meta: {
            //     id: ''
            //   }
            // },
            // {
            //   name: '日志管理',
            //   url: '',
            //   backPosition: '0 -160px',
            //   meta: {
            //     id: ''
            //   }
            // }
          ]
        }
      ],
      /* 路由高亮状态 */
      navActiveState: -1,
      /* 二级路由框展示状态 */
      littleNavState: -1,
      /* 当前点击的导航的index */
      theIndex: -1
    }
  },
  watch: {
    $route (to) {
      this.navActiveState = this.judgeRouter(to.path)
    }
  },
  mounted () {
    document.addEventListener('click', this.littleNavHide)
    this.navActiveState = this.judgeRouter(this.$route.path)
    // let arr = []
    // if (this.$store.getters.getloginInfor() !== null) {
    //   for (let item of this.$store.getters.getloginInfor().resource) {
    //     arr.push(item.id)
    //   }
    // }
    // for (let i in this.data) {
    //   this.data[i].children = this.data[i].children.filter(wes => arr.indexOf(wes.meta.id) !== -1)
    // }
    // this.data = this.data.filter(wes => arr.indexOf(wes.meta.id) !== -1)
  },
  beforeDestroy () {
    document.removeEventListener('click', this.littleNavHide)
  },
  methods: {
    /* 一级导航router判断 */
    judgeRouter (url) {
      // 红线信息服务
      if (url.indexOf('/main/dataCenter') !== -1) {
        return 1
      }
      // 勘界定标内业管理
      if (url.indexOf('/main/circlesMark') !== -1) {
        return 2
      }
      // 项目准入核查
      if (url.indexOf('/main/projectApproval') !== -1) {
        return 5
      }
      // 人类活动遥感监测
      if (url.indexOf('/main/monitor') !== -1) {
        return 6
      }
      // 核查任务管理
      if (url.indexOf('/main/dynamicManage') !== -1) {
        return 13
      }
      // 系统管理
      if (url.indexOf('/main/systemManage') !== -1) {
        return 21
      }
    },
    /* 点击任意地方隐藏二级导航 */
    littleNavHide (e) {
      if (this.littleNavState !== -1) {
        if ((!this.$refs.littleNav[this.theIndex].contains(e.target)) && (!this.$refs.nav[this.theIndex].contains(e.target))) {
          this.littleNavState = -1
        }
      }
    },
    /* 删除登录信息 */
    delLogin () {
      this.$store.commit('setloginInfor', {})
      sessionStorage.removeItem('loginInfo')
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  #header{
    border-bottom:none ;
    position: relative;
    background: url("../../../assets/main_images/navBack.png") no-repeat;
    background-size: 100% 100%;
    height: 70px;
    /*logo*/
    .title {
      width: 25%;
      position: absolute;
      left: 20px;
      top: 50%;
      transform: translateY(-50%);
    }
    /*右上角登录状态栏样式*/
    .statusColumn{
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
      img{
        vertical-align: middle;
        width: 25px;
        margin-right: 8px;
      }
      span{
        font-size: 14px;
        strong{
          color: #fff;
          font-weight: normal;
        }
      }
      .logout {
        a {
          font-weight: normal;
          color: #fe8283;
          text-decoration: underline;
          margin-left: 15px;
        }
      }
    }
    /*右下角导航样式*/
    .nav{
      overflow: visible;
      zoom: 1;
      position: absolute;
      right:18%;
      bottom:0;
      >li{
        float:left;
        padding:5px 10px 0;
        cursor:pointer;
        .navP{
          border-radius: 5px 5px 0 0;
          padding:20px 5px;
          strong{
            display: inline-block;
            width: 30px;
            height:30px;
            vertical-align:middle;
            margin-right: 10px;
            background: url("../../../assets/main_images/nav.png");
            background-size: 100% auto;
          }
        }
        .navActive{
          color: #fff;
          border-bottom: 3px solid #1f7efe;
          strong{
            width: 30px;
            height:30px;
            background: url("../../../assets/main_images/nav_white.png");
            background-size: 100% auto;
          }
        }
        span{
          color: #fff;
          font-size: 14px;
          vertical-align:middle;
          letter-spacing: 1px;
        }
        .littleNav{
          position: absolute;
          top: 70px;
          left: -11px;
          background: #54637f;
          border: 1px #c5c5c5 solid;
          border-top: 0;
          z-index: 53615361;
          width: calc(100% + 25px);
          border-radius: 0 0 3px 3px;
          .littleNavP{
            margin:4px 0;
            padding:8px 10px;
            border-radius: 0;
            strong{
              display: inline-block;
              width: 20px;
              height:20px;
              vertical-align:middle;
              margin-right: 3px;
              color: #fff;
            }
            span {
              font-size: 12px;
            }
          }
          a.router-link-active>.littleNavP{
            background: #1DA6CE;
            color:#fff;
            strong{
              width: 20px;
              height:20px;
            }
          }
        }
      }
    }
    .f14 {
      font-size: 14px;
    }
  }
</style>
